import React, { Component, Fragment } from 'react';
import { BtnDelete } from 'components/common/button';
import { Table, Popconfirm } from 'antd';
import { ScholarshipApproval } from 'dump/data';

const columns = [
  {
    title: 'Mã sinh viên',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'TBCHT10',
    dataIndex: 'min10',
    key: 'min10',
  },
  {
    title: 'Xếp loại HT10',
    dataIndex: 'ratings10',
    key: 'ratings10',
  },
  {
    title: 'TBCHT4',
    dataIndex: 'min4',
    key: 'min4',
  },
  {
    title: 'Xếp loại HT4',
    dataIndex: 'ratings4',
    key: 'ratings4',
  },
  {
    title: 'Tổng điểm RL',
    dataIndex: 'toltalRL',
    key: 'toltalRL',
  },
  {
    title: 'Xếp loại RL',
    dataIndex: 'ratingsRL',
    key: 'ratingsRL',
  },
  {
    title: 'Xếp loại HB',
    dataIndex: 'ratingsHB',
    key: 'ratingsHB',
  },
  {
    title: 'Số tiền HB',
    dataIndex: 'toltalHB',
    key: 'toltalHB',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      ScholarshipApproval.length >= 1 ? (
        <Fragment>
          {/* <BtnEdit href={`#ScholarshipApproval/form/edit/${record.codeRating}`} /> */}
          <Popconfirm 
            title="Bạn có muốn xóa?" 
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.handleDelete(record.key)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};

class TableScholarship extends Component {
  state = {};

  render() {
    return (
      <Table
        rowSelection={rowSelection}
        bordered
        dataSource={ScholarshipApproval}
        columns={columns}
        scroll={{ x: true }}
      />
    );
  }
}

export default TableScholarship;



