import React, { Component } from 'react';
import { Row, Col, Form, Select, Input, Checkbox, Button, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { YearSemesterInput } from 'components/common';

import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  toggle = () => {
    const { expand } = this.state;
    this.setState({ expand: !expand });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
          <YearSemesterInput form={this.props.form} />

          <Col span={6}>
            <Form.Item label="Xuất sắc">{getFieldDecorator('system11', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Giỏi">{getFieldDecorator('system12', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khá">{getFieldDecorator('system13', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phương án xét">
              {getFieldDecorator('system7', {
                rules: [
                  {
                    required: true,
                    message: 'Input something!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Cấp trường</Option>
                  <Option value="2">Cấp tỉnh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Xét theo số xuất">
              {getFieldDecorator('system8', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Xét theo số tiền">
              {getFieldDecorator('system10', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label="Xét theo % sinh viên được học bổng">
              {getFieldDecorator('system9', {})(<Input />)}
            </Form.Item>
          </Col>

          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system14', { valuePropName: 'checked' })(
                <Checkbox>Không tính điểm môn học lại </Checkbox>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system15', { valuePropName: 'checked' })(
                <Checkbox>Lấy hơn </Checkbox>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system16', { valuePropName: 'checked' })(
                <Checkbox>Lấy kém </Checkbox>
              )}
            </Form.Item>
          </Col>
          {/* <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system19', {})(<Checkbox> Chọn tất cả </Checkbox>)}
            </Form.Item>
          </Col> */}
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system17', {})(<Radio>Chưa nhận được học bổng</Radio>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item>
              {getFieldDecorator('system18', {})(<Radio>Đã nhận được học bổng</Radio>)}
            </Form.Item>
          </Col>

          <Col span={6}>Số tiền đã xét : 10</Col>
          <Col span={6}>Số sv được học bổng : 10</Col>
          <Col span={6}>% sv được học bổng : 8</Col>
          <Col span={6}>Số sinh viên : 8</Col>
          <Col span={6}>Số tiền đã xét : 8</Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
              <Button style={{ height: 38 }} type="primary">
                Xét học bổng
              </Button>
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



